* {
  box-sizing: border-box;
  
}


html,
body {
  margin: 0;
  padding: 0;
  
}

body {
  background: linear-gradient(to right, #050d1a, #16235a, #4e106b);
}


/* Scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(228, 228, 228)
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #72337a;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b973fa;
}